<template>
  <user>
    <html-head 
      v-if="user.data.name"
      :title="user.data.name"
    ></html-head>

    <page-header
      :to="{ name: 'users.index' }"
    >
      <template v-slot:title >
        <span v-text="`Edit User`"></span>
      </template>
    </page-header>

    <edit-skeleton v-if="user.isFetching" ></edit-skeleton>
    <v-row :class="{ 'd-none': user.isFetching }">
      <v-col cols="12" lg="6" class="mx-auto">
        <validation-observer ref="validation">
        <v-form ref="form" @submit.prevent="submit" enctype="multipart/form-data" :disabled="user.isFetching">
          <v-card elevation="0">
            <v-card-title v-text="`Edit Information`"></v-card-title>
            <v-card-text>
              <form-errors :error="user.errors.message"></form-errors>
              <validation-provider vid="firstName" v-slot="{ errors }">
                <v-text-field
                  :error-messages="errors"
                  label="First Name"
                  name="firstName"
                  outlined
                  v-model="user.data.firstName"
                  @input="user.prestine( false )"
                ></v-text-field>
              </validation-provider>
              <validation-provider vid="lastName" v-slot="{ errors }">
                <v-text-field
                  :error-messages="errors"
                  label="Last Name"
                  name="lastName"
                  outlined
                  v-model="user.data.lastName"
                  @input="user.prestine( false )"
                ></v-text-field>
              </validation-provider>
              <validation-provider vid="email" v-slot="{ errors }">
                <v-text-field
                  :error-messages="errors"
                  label="E-mail"
                  name="email"
                  outlined
                  v-model="user.data.email"
                  @input="user.prestine( false )"
                ></v-text-field>
              </validation-provider>
              <validation-provider vid="role" v-slot="{ errors }">
                <role-selector v-model="user.data.role" @input="user.prestine( false )" :errors="errors"></role-selector>
              </validation-provider>
              <!-- <input type="hidden" name="creator" :value="auth.user._id"> -->
            </v-card-text>
            <v-card-actions class="d-flex justify-end"> 
              <v-btn color="primary" type="submit" :loading="user.isFetching" :disabled="user.isPrestine">Save</v-btn>
            </v-card-actions>
          </v-card>
        </v-form>
        </validation-observer>
      </v-col>
      <v-col cols="12" lg="6">
        <image-uploader :src="user.data.image" title="Edit Image" :url="api.uploadImage( $route.params.id )"></image-uploader>
      </v-col>
    </v-row>
  </user>
</template>

<script>
import Api from './routes/api'
import User from './models/User'
import { mapActions, mapGetters } from 'vuex'

export default {
  components: {
    EditSkeleton: () => import('./cards/EditSkeleton')
  },

  computed: {
    ...mapGetters({
      auth: 'auth/auth',
    }),
  },

  data: () => ({
    api: Api,
    user: new User,
  }),

  methods: {
    ...mapActions({
      setSnackbar: 'snackbar/set',
      setAuth: 'auth/set',
    }),

    async submit () {
      const data = await this.user.request({
        method: 'patch',
        url: Api.alone( this.$route.params.id ),
        data: this.user.readyData( this.$refs.form.$el )
      })

      console.log( this.$refs )

      if( !data ) 
        return this.$refs.validation.setErrors( this.user.errors.details )
        
      this.setSnackbar({
        model: true,
        text: 'User updated',
        timeout: 3000
      })

      if( data._id === this.auth.user._id )
        this.setAuth({ user: { ...this.user.data } })
    }
  },

  mounted () {
    this.user.getResource( Api.alone( this.$route.params.id ) )
    this.user.load( false )
  }
}
</script>